.scroll-box {
    display: flex;
    width: 100vw;
    height: 100vh;
    overflow-y:auto
}
.scroll-box.vertical {
    flex-direction: column;
    overflow: hidden auto;
    scroll-snap-type: y mandatory;
}
.item {
    scroll-snap-align: center;
    align-items: center;
    justify-content: center;
}

.item video{
    filter: brightness(80%);
    grid-area: 1/-1;
    z-index: 2;
}

.item img{
    width: 100%;
    height: 1100px;
    object-fit: cover;
}

@keyframes accodian {
    0% {
        opacity: 0;
        transform: translate3d(0, -10px, 0);
    }
    100% {
        opacity: 1;
        transform: none;
      }
}
.header{
    position:absolute;
    display:flex;
    flex-direction: column;
    width:100%;
    justify-content: space-between;
    align-items: center;
    top:0;
    left: 0;
    right: 0;
    z-index:500;
}

.header_menu{
    display:flex;
    justify-content: space-between;
    align-items: center;
    width:100%;
}

@keyframes menu {
    0%{
        opacity: 0;
        transform: translate3d(0 100% 0);
    }
    100%{
        opacity: 1;
        transform: none;
    }
}
.tesla_logo{
    display:flex;
    padding: 0px 16px 0px 16px;
    margin-left:32px;
    animation: menu 1s;
}

.menu{
    list-style: none;
    padding:0px 8px 0px 8px;
    margin:0px 20px 0px 0px;
    font-size:14px;
    display:flex;
    color:white;
    align-items: center;
}

.menu li{
    margin:0px 8px 0px 8px;
    padding: 4px 8px 4px 8px;
    animation: menu 1s;
    cursor: pointer;
}

.icon{
    list-style: none;
    padding:0px;
    margin:0px;
    display:flex;
    justify-content: space-around;
    font-size:14px;
    margin-right:32px;
}

.icon li a{
    padding: 4px 6px 4px 6px;
    margin:0px 4px 0px 0px;
    animation: menu 1s;
}

@keyframes fadeInUp_h2 {
    0% {
        opacity: 0;
        transform: translate3d(0, 0px, 0);
    }
    50%{
        opacity: 0;
        transform: translate3d(0, 40px, 0);
    }
    100% {
        opacity: 1;
        transform: none;
      }
}

.banner{
    position: fixed;
    top:0;
    left:50%;
    transform: translateX(-50%);
    grid-area: 1/-1;
    z-index: 3;
    text-align: center;
}

.banner_column h2{
    color:white;
    font-size:40px;
    font-weight:500;
    margin:120px 0px 0px 0px;
    animation: fadeInUp_h2 2s;  
}

@keyframes fadeInUp_p {
    0% {
        opacity: 0;
        transform: translate3d(0, 0, 0);
    }
    70%{
        opacity: 0;
        transform: translate3d(0, 40px, 0);
    }
    100% {
        opacity: 1;
        transform: none;
      }
}

.banner_column p{
    animation: fadeInUp_p 2.5s;
}

.banner_column a{
    color:white;
}

@keyframes slideIn_a{
    0%{
        opacity: 0;
        transform: translate3d(0, 0, 0);
    }
    70%{
        opacity: 0;
        transform: translate3d(-60%, 0, 0);
    }
    100%{
        opacity: 1;
        transform: none;
    }
}

@keyframes slideIn_b{
    0%{
        opacity: 0;
        transform: translate3d(0, 0, 0);
    }
    70%{
        opacity: 0;
        transform: translate3d(60%, 0, 0);
    }
    100%{
        opacity: 1;
        transform: none;
    }
}

.banner_row{
    position: absolute;
    display:flex;
    justify-content: space-between;
    left: 50%;
    transform: translateX(-50%);
    width:560px;
    bottom:-650px;
}

.banner_row button{
    padding: 11px 60px 11px 60px;
    border-radius: 5px;
    font-size:14px;
    cursor: pointer;
}

.banner_row button:first-child{
    background-color: #3d3a39;
    color:white;
    border: none;
    animation: slideIn_a 2.5s;
}

.banner_row button:last-child{
    background-color: #b3b2b8;
    color: #4f5050;
    border: none;
    animation: slideIn_b 2.5s;
}

